import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../../styles/Notice.css";
import Button from "../../components/Button.jsx";
import { useTranslation } from "react-i18next";

function NoticeList({ notices }) {
  const { t } = useTranslation();
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const noticesPerPage = 5;

  // 1)    ( )
  const filteredNotices = notices.filter((notice) =>
    (notice.title || "")
      .toString()
      .toLowerCase()
      .includes(searchTerm.toLowerCase())
  );

  // 2)   
  const sortedNotices = [...filteredNotices].sort(
    (a, b) => new Date(b.date) - new Date(a.date)
  );

  // 3) 
  const indexOfLastNotice = currentPage * noticesPerPage;
  const indexOfFirstNotice = indexOfLastNotice - noticesPerPage;
  const currentNotices = sortedNotices.slice(
    indexOfFirstNotice,
    indexOfLastNotice
  );

  // 4)   
  const totalPages = Math.ceil(sortedNotices.length / noticesPerPage);

  // 5)  
  const pageNumbers = Array.from({ length: totalPages }, (_, i) => i + 1);

  return (
    <div className="notice-list-container">
      <br />
      <br />
      <h1>{t("notice.list.title", { defaultValue: "" })}</h1>
      <br />
      <br />

      {/*  */}
      <div className="search-box">
        <input
          type="text"
          placeholder={t("notice.list.searchPH", {
            defaultValue: " ",
          })}
          value={searchTerm}
          onChange={(e) => {
            setSearchTerm(e.target.value);
            setCurrentPage(1); //    1
          }}
          aria-label={t("notice.list.searchLabel", { defaultValue: "" })}
        />
        <button
          aria-label={t("notice.list.searchBtn", { defaultValue: "" })}
        >
          
        </button>
      </div>

      {/*   */}
      <table
        className="notice-table"
        aria-label={t("notice.table.label", { defaultValue: " " })}
      >
        <thead>
          <tr>
            <th>{t("notice.table.no", { defaultValue: "" })}</th>
            <th>{t("notice.table.title", { defaultValue: "" })}</th>
            <th>{t("notice.table.writer", { defaultValue: "" })}</th>
            <th>{t("notice.table.date", { defaultValue: "" })}</th>
          </tr>
        </thead>
        <tbody>
          {currentNotices.length === 0 ? (
            <tr>
              <td colSpan="4" className="no-data">
                {t("notice.list.empty", {
                  defaultValue: " .",
                })}
              </td>
            </tr>
          ) : (
            currentNotices.map((notice, index) => (
              <tr key={notice.id}>
                <td>{sortedNotices.length - (indexOfFirstNotice + index)}</td>
                <td className="title-cell">
                  <Link to={`/notice/${notice.id}`}>{notice.title}</Link>
                </td>
                <td>{notice.writer}</td>
                <td>{notice.date}</td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {/*   */}
      <div style={{ textAlign: "right" }}>
        <br />
        <Link to="/notice/write" className="btn">
          <Button variant="white">
            {t("notice.list.writeBtn", { defaultValue: "" })}
          </Button>
        </Link>
      </div>

      {/*  */}
      <div className="pagination" style={{ marginTop: "20px" }}>
        <button
          onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
          disabled={currentPage === 1}
          aria-label={t("common.prev", { defaultValue: "" })}
        >
          {"<"}
        </button>

        {pageNumbers.map((num) => (
          <button
            key={num}
            className={num === currentPage ? "active" : ""}
            onClick={() => setCurrentPage(num)}
            aria-current={num === currentPage ? "page" : undefined}
          >
            {num}
          </button>
        ))}

        <button
          onClick={() =>
            setCurrentPage((prev) => Math.min(prev + 1, totalPages))
          }
          disabled={currentPage === totalPages || totalPages === 0}
          aria-label={t("common.next", { defaultValue: "" })}
        >
          {">"}
        </button>
      </div>
    </div>
  );
}

export default NoticeList;
