import React from 'react';
import '../styles/Button.css'; // css variant   css  ( black/white !)

const Button = ({
  children,
  onClick,
  variant = 'default',
  type = 'button',
  disabled = false,
  className = '',
}) => {
  return (
    <button
      type={type}
      className={`btn ${variant} ${className}`}
      onClick={onClick}
      disabled={disabled}
    >
      {children}
    </button>
  );
};

export default Button;
