import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "../../styles/Notice.css";
import { useTranslation } from "react-i18next";

function NoticeWrite({ addNotice }) {
  const { t } = useTranslation();
  const [title, setTitle] = useState("");
  const [writer, setWriter] = useState("");
  const [content, setContent] = useState("");
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();

    // 1) :  
    if (typeof addNotice !== "function") {
      console.error("addNotice is not a function:", addNotice);
      alert(
        t("notice.write.missingAddNotice", {
          defaultValue:
            "     . /props   .",
        })
      );
      return;
    }

    // 2)  
    try {
      await addNotice(title, writer, content);
      navigate("/notice"); //   
    } catch (err) {
      console.error("Failed to add notice:", err);
      alert(
        t("notice.write.error", {
          defaultValue: "   .   .",
        })
      );
    }
  };

  return (
    <div className="notice-write-container">
      <h2 className="notice-write-title">
        {t("notice.write.title", { defaultValue: " " })}
      </h2>

      <form className="notice-write-form" onSubmit={handleSubmit}>
        <label>
          {t("notice.write.fieldTitle", { defaultValue: "" })}:
          <input
            type="text"
            placeholder={t("notice.write.phTitle", { defaultValue: "" })}
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            required
            aria-label={t("notice.write.fieldTitle", { defaultValue: "" })}
          />
        </label>

        <label>
          {t("notice.write.fieldWriter", { defaultValue: "" })}:
          <input
            type="text"
            placeholder={t("notice.write.phWriter", {
              defaultValue: "",
            })}
            value={writer}
            onChange={(e) => setWriter(e.target.value)}
            required
            aria-label={t("notice.write.fieldWriter", {
              defaultValue: "",
            })}
          />
        </label>

        <label>
          {t("notice.write.fieldContent", { defaultValue: "" })}:
          <textarea
            placeholder={t("notice.write.phContent", {
              defaultValue: "",
            })}
            value={content}
            onChange={(e) => setContent(e.target.value)}
            required
            rows={10}
            aria-label={t("notice.write.fieldContent", {
              defaultValue: "",
            })}
          />
        </label>

        <div className="button-group">
          <button type="submit" className="submit-button">
            {t("notice.write.saveBtn", { defaultValue: "" })}
          </button>
          <button
            type="button"
            className="back-button"
            onClick={() => navigate(-1)}
          >
            {t("notice.write.backBtn", { defaultValue: "" })}
          </button>
        </div>
      </form>
    </div>
  );
}

export default NoticeWrite;
